@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

.App {
  text-align: center;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  height: 100vh;
  flex-direction: column;
  background-color: #111;
  color: aliceblue;

  position: relative;
}

.projectinfo {
  position: absolute;
  display: flex;
  align-items: flex-end;

  right: 0;
  bottom: 7.5%;
  font-size: 2rem;
  opacity: .6;
  width: 52px;
  height: 45px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: rgb(90, 85, 85);


}

.projectinfo a {
  text-decoration: none;
  color: white;

  padding-left: 5px;
}

.projectinfo:hover {
  opacity: 1;
  width: 100px;
  transition: .4s all ease;
  justify-content: flex-start;


}

.App h2 {
  font-size: 2.4rem;
  font-weight: 500;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 2px;
  margin-bottom: -15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
}

.heads {
  display: flex;
  flex-direction: row;
}

.App h2::after {
  content: "";
  width: 150px;
  height: 4px;
  background: deeppink;
  border-radius: 20px;
  margin-top: 3px;
}

.App h2:hover::after {

  animation: underline .3s linear;
}

@keyframes underline {
  0% {
    width: 200px;
  }

  100% {
    width: 150px;
  }
}

.t2i {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  width: 80vw;
  /* border: 2px solid red; */
  position: relative;
}

.inputText {
  width: 40vw;
  border: 3px solid rgb(26, 16, 99);

  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  background-color: black;
  border-radius: 10px;
  position: absolute;
  bottom: 30px;


}

.inputText:focus-within {
  border: 3px solid white;
  transition: .3s all ease;
}

.downloadBtn {
  position: absolute;
  right: 30px;
  bottom: -28px;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;
  background-color: black;
  border-radius: 50%;
  border: 4px solid rgb(255, 0, 0);
  ;
}

.downloadBtn .down {
  font-size: 1.8rem;
  cursor: pointer;
  color: lime;
}

.downloadBtn .down:hover,
.downloadBtn:hover {
  transform: scale(1.1);
  transition: .3s all ease;
}


.inputText input {

  width: 35vw;

  font-size: 1rem;
  background-color: transparent;
  border: none;
  color: white;
}

.inputText input:focus {
  border: none;
  outline: none;
}

.inputText input::placeholder {
  color: aliceblue;
}

.inputText button {
  height: 35px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.8rem;
}

.inputText button svg {
  display: flex;
  align-items: center;
  justify-content: center;
  color: yellowgreen;

}

.inputText button svg:hover {
  color: lime;
  transition: .3s all ease;
}

@media(max-width: 951px) {
  .inputText {
    width: 80vw;
  }

  .inputText input {
    width: 70vw;
  }
}

@media(max-width: 551px) {
  .inputText {
    width: 90vw;
    padding: 5px;
  }

  .inputText input {
    width: 84vw;
  }
}


.outputMain {
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
  padding: 10px;
  position: relative;
  /* overflow: hidden; */

  border: 4px solid rgb(255, 0, 0);
  border-radius: 15px;


}

.outputMain img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
}

@media (max-width: 768px) {
  .t2i {
    padding-left: 5%;
    padding-right: 5%;
    position: relative;
  }

  .outputMain {
    position: absolute;
    width: 80vw;
  }
}

@media (max-width: 480px) {
  .projectinfo {
    font-size: 1.6rem;
    height: 38px;
  }

  .t2i {
    align-items: unset;
  }
}

.footer {
  background-color: #111;
}

.footer a {
  text-decoration: none;
  color: crimson;
}

.tryLogin {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

.mainlogo {
  margin-bottom: 30px;
}

.mainlogo h2 {
  margin-bottom: 0;
}

.tryLogin img {
  border: 3px solid white;
  width: 100px;
  height: 100px;
  margin-top: 10px;

}


.userFaculity {
  margin-top: 10px;
  height: 50px;
  width: 100px;
  position: absolute;
  /* border: 2px solid white; */
  left: 20px;
  bottom: 14%;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.userFaculity .user {
  outline: 2px solid white;
  outline-offset: 3px;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.userFaculity .logoutBtn {
  margin-left: 15px;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}

.userFaculity .logoutBtn:hover {
  transition: .3s all ease;
  color: crimson;
  transform: scale(.95);
}

.userFaculity img {
  max-width: 40px;
  max-height: 40px;
  border-radius: 50%;
}

/* Navbar Styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: rgb(26, 16, 99);
  color: #fff;
  height: 30px;
}

.navbar-logo {
  font-size: 24px;
  font-weight: bold;
}

.navbar-links {
  display: flex;
  /* flex-direction: row; */
  /* align-items: center; */
  gap: 10px;
  gap: 50px;
  margin-right: 50px;
}



/* Style for Links */
.navbar-links a {
  text-decoration: none;
  color: white;
  font-weight: 500;
}

.navbar-links a:hover {
  color: yellow;
  transition: .3s all ease;
}

.navbar-toggle {
  display: none;
}



.bar {
  width: 25px;
  height: 30px;
  /* margin: 3px 0; */
  transition: transform 0.3s, opacity 0.3s;
}

/* Media Query for Responsive Navbar */
@media screen and (max-width: 768px) {
  .navbar-links {
    display: none;
  }

  .navbar-links.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 70px;
    right: -10px;
    /* border: 2px solid white; */
    width: 40vw;
    height: 100vh;
    background-color: rgb(26, 16, 99);
    gap: 50px;
    padding: 20px;
    border-left: 10px solid white;

  }


  .navbar-toggle {
    display: flex;
    cursor: pointer;
    margin-right: 50px;
  }
}


.content {
  margin-top: 70px;
}


.pleaseBG {
  background-color: #111;
  width: 100vw;
}

.hero {
  padding: 15px;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  gap: 15px;
  margin-top: 40px;
}

.set1 {
  width: 45vw;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 40px;
  gap: 10px;
  margin-top: 40px;
}

.set1 h2 {
  font-size: 4.5rem;
}

.set1 p {

  font-size: 2rem;
}

.set1 span {
  font-style: italic;
  color: crimson;

}

.set1 span:hover {
  font-style: normal;
  transition: .4s all ease-in;
}

.set1 button {
  padding: 10px 15px;
  width: 180px;
  font-size: 1.2rem;
  cursor: pointer;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;

}

.set1 button:hover {
  transition: .4s all ease;
  background-color: #111;
  outline: 3px solid white;
  outline-offset: 3px;
  color: #fff;
}

.set1 .redirect {
  margin-left: 4px;
  color: orange;
  font-size: 1.8rem;
}

.set2 {
  width: 45vw;
}

.set2 svg:hover {
  transform: rotate(360deg);
  transition: transform .4s;
}

.iconhere {
  color: coral;
  height: 400px;
  width: 45vw;
}

@media screen and (max-width: 768px) {

  .hero {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .set1 {
    align-items: center;
    margin-left: unset;
    margin-top: unset;
  }

  .iconhere {
    color: coral;
    height: 400px;
    width: 55vw;
  }
}